import React from 'react'
import clsx from 'clsx'

export type IconName =
  | 'arrowDown'
  | 'arrowLeft'
  | 'arrowRight'
  | 'arrowUp'
  | 'close'

/**
 * The icon's size.
 *
 * - Small: 1rem
 * - Large: 2rem
 */
export type IconSize = 'small' | 'large'

export interface IconProps extends React.HTMLAttributes<HTMLElement> {
  /**
   * The icon's name.
   */
  name: IconName

  /**
   * The icon's size.
   *
   * @default 'medium'
   */
  size?: IconSize
}

/**
 * Icon.
 */
export const Icon: React.FC<IconProps> = ({
  className,
  name,
  size = 'small',
  ...rest
}) => (
  <span
    className={clsx(
      'dib tc',
      size === 'small' && 'h1 w1',
      size === 'large' && 'h2 w2',
      className
    )}
    {...rest}
  >
    {name === 'arrowDown' && '↓'}
    {name === 'arrowLeft' && '←'}
    {name === 'arrowRight' && '→'}
    {name === 'arrowUp' && '↑'}
    {name === 'close' && '✕'}
  </span>
)
try {
    // @ts-ignore
    Icon.displayName = "Icon";
    // @ts-ignore
    Icon.__docgenInfo = { "description": "Icon.", "displayName": "Icon", "props": { "name": { "defaultValue": null, "description": "The icon's name.", "name": "name", "required": true, "type": { "name": "IconName" } }, "size": { "defaultValue": { value: "small" }, "description": "The icon's size.", "name": "size", "required": false, "type": { "name": "IconSize" } } } };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["components/library/Icon.tsx#Icon"] = { docgenInfo: Icon.__docgenInfo, name: "Icon", path: "components/library/Icon.tsx#Icon" };
}
catch (__react_docgen_typescript_loader_error) { }